<template>
    <div class="qiong-td-fix qiong-pt-10 pl-7 pr-7">
        
        <div class="qiong-txt-td mb-1">
            現在個人支付級別
        </div>

        <div class="br-simple circle-XL qiong-pl-20 qiong-pr-20 pt-3 pb-3 bg-simple">
            <div class="qiong-td">
                <div class="qiong-wide-25 flex-center pr-0 qiong-pt-5">
                    <level-image-viewing :bigger="2" 
                        :item="me.vip.named + '_' + me.vip.star"
                        :extra_class="'vip_showing'"
                        class="w-full"
                    ></level-image-viewing>
                </div>
                <div class="qiong-wide-75 fw-b">
                    <table class="w-full qiong-line-hight-15">
                        <tr v-for="(v, i) in result" :key="i">
                            <td>{{ v.txt }}</td>
                            <td class="text-right">{{ v.content }}</td>
                        </tr>
                    </table>
                </div>
            </div>
        </div>

        <div class="qiong-txt-td mb-1 qiong-pt-10 mt-2">
            <div v-if="next_vip">
                到達下一級&nbsp;
                    <div class="d-inline txt-focus">{{ next_vip.nick }}</div>
                    <!--v-menu offset-y>
                        <template v-slot:activator="{ on, attrs }">
                            
                            <v-btn shaped text 
                                class=" px-0 btn-none btn-menu-fix qiong-txt-td txt-focus" 
                                v-on="on" v-bind="attrs">

                                <div class="qiong-txt-underline d-inline txt-focus hand">{{ next_vip.nick }}</div>
                            </v-btn>
                        </template>
                        <v-list>
                            <v-list-item
                            v-for="(item, index) in vip_items"
                            :key="index"
                            >
                                <v-list-item-title>{{ item.title }}</v-list-item-title>
                            </v-list-item>
                        </v-list>
                    </v-menu-->
                &nbsp;所需要完成的任務
            </div>
            <div v-else>
                您現在已是最高會員等級&nbsp;<div class="d-inline txt-focus" v-if="next_vip">{{ next_vip.nick }}</div>
            </div>
        </div>

        <div v-for="(v, i) in result" :key="i">
            <div class="bg-simple qiong-mb-10 circle-XL pt-1 pb-1">
                <div class="qiong-td">
                    <div class="qiong-wide-48 qiong-pl-20 pr-0">
                        {{ v.txt }}
                    </div>
                    <div class="qiong-wide-27 text-right pr-1">
                        {{ v.content }}/{{ v.target }}
                    </div>
                    <div class="qiong-wide-25 qiong-table-fix pr-0">
                        <finised-btn-viewing :rec="Number.parseInt(v.content)" :target="Number.parseInt(v.target)" class=""></finised-btn-viewing>
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
import QiongAvatarColor from '../../../../components/Qiong/Ui/QiongAvatarColor.vue'
import FinisedBtnViewing from '../../../../components/Viewing/FinisedBtnViewing.vue'
import LevelImageViewing from '../../../../components/Viewing/LevelImageViewing.vue'
    export default {
    components: { QiongAvatarColor, LevelImageViewing,
        FinisedBtnViewing
    },
        props: {
            me: {
                type: Object
            }
        },
        name: '',
        data() {
            return {
                next_vip: null,
                finished: false,

                next_vip_condition: null,

                vip_items: [ ]
            }
        },
        computed: {

            result() {  
                if (this.next_vip) {
                    return [
                        {
                            txt: '每周期個人銷售活躍點數',
                            content: this.me.ipv,
                            target: this.next_vip.condition[0], unit: 'PV'
                        },
                        {
                            txt: '每周期團隊銷售點數',
                            content: this.me.transfer.son_pv_SPONSER,
                            target: this.next_vip.condition[1], unit: 'PV'
                        },
                        {
                            txt: '合資格下線',
                            content: this.me.transfer.son_can_num_ENROLLER,
                            target: this.next_vip.condition[2], unit: ''
                        },
                        {
                            txt: '合資格下線團隊銷售點數',
                            content: this.me.transfer.ipv_next_only_ENROLLER,
                            target: this.next_vip.condition[3], unit: 'PV'
                        }
                    ]
                }
            } 
        },
        mounted() {
            this.next_vip = this.bonus.vip_view.vip_next(this.me.bonus.vip)
            this.finished = this.bonus.vip_view.finished_vip(this.me.vip.name, this.me.vip.star)

            console.log(this.me.bonus.vip, ' 下一個VIP =', this.next_vip.name, this.finished,)
        },
    }
</script>

<style lang="sass" scoped>
    
</style>


