<template>
    <div>
        <div class="qiong-btn btn-finished " 
            v-if="is_finished"
        >
            <div class="d-inline">
                <v-icon size="16">mdi-check</v-icon>
            </div>
            <div class="d-inline-block qiong-txt-12">
                已完成
            </div>
        </div>
        <div class="qiong-btn btn-unfinished qiong-txt-12" 
            v-else
        >
            <div class="d-inline">
                <v-icon size="16">mdi-window-close</v-icon>
            </div>
            <div class="d-inline-block">
                未完成
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: '',
        props: {
            rec: {
                type: Number
            },
            target: {
                type: Number
            }
        },
        computed: {
            is_finished() {
                return (this.rec >= this.target)
            }
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


